import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { readRemoteFile } from "react-papaparse";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from "../../components/footer";
import { ImgBlock } from "../../components/imgBlock";
import Loader from "react-loader-spinner";
import param from "../../param";
import { motion, AnimatePresence } from "framer-motion";
// import peopleData from "./peopleData";

function People(props) {
  const [peopleData, setPeopleData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vTcYoTeu8ot1szU438hpiE-5KWTbkleW5LVI4COzcE3Qrq-f2KPd2kJy90pGae2CxKIaHkAMi8ybroy/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          setPeopleData(results.data);
          setGetData(true);
        },
      });

    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '實驗室成員', en: 'Lab Member' }} />
        <div className='people'>
          <PeoplePage data={peopleData} />
        </div>
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '實驗室成員', en: 'Lab Member' }} />
        <div className='people'>
          <div className="text-center py-5">
            <Loader type="Grid" color="#403E3E" height={80} width={80} />
          </div>
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { People };

function PeoplePage(props) {
  // preProcess
  const [member, setMember] = useState([])
  const [loaded, setLoaded] = useState(false)
  const [expanded, setExpanded] = useState(0)

  useEffect(() => {

    const processData = () => {

      let tmp_memberdata = {}

      //currentMember formerMember professionalCollaborators principalInvestiagtor

      for (let index = 1; index < props.data.length; index++) {
        // [type] create if not exist
        if (!(props.data[index][1] in tmp_memberdata)) {
          tmp_memberdata[props.data[index][1]] = {}
        }

        // [position] create if not exist
        if (!(props.data[index][2] in tmp_memberdata[props.data[index][1]])) {
          tmp_memberdata[props.data[index][1]][props.data[index][2]] = []
        }

        // add data
        tmp_memberdata[props.data[index][1]][props.data[index][2]].push({
          name: props.data[index][3],
          image: props.data[index][4],
          description: props.data[index][5],
          time: props.data[index][6],
          project: props.data[index][7]
        })

      }

      const dict2list = (type) => {
        let list = []
        Object.keys(tmp_memberdata[type]).forEach(key => {
          list.push({
            type: key,
            data: tmp_memberdata[type][key]
          })
        })
        return list
      }

      let memberdata = []
      let typeOrder = ['指導教授 Principal Investiagtor', '目前成員 Current Member', '先前成員 Former Member', '合作 Professional Collaborators']
      typeOrder.forEach(typeName => {
        memberdata.push({
          type: typeName,
          data: dict2list(typeName)
        })
      })

      setMember(memberdata)
      setLoaded(true)
    }

    if (!loaded) processData()
  }, [loaded])


  // render
  if (loaded) {

    const accordionIds = [...Array(member.length).keys()];
    return accordionIds.map((i) => (
      <Accordion key={i} idx={i} expanded={expanded} setExpanded={setExpanded} list={member[i]['data']} title={member[i]['type']} />
    ));
  } else {
    return (
      <div>

      </div>
    )
  }


}

function Accordion({ idx, expanded, setExpanded, list, title }) {
  const isOpen = (idx === expanded);

  // By using `AnimatePresence` to mount and unmount the contents, we can animate
  // them in and out while also only rendering the contents of open accordions
  return (
    <Container>
      <motion.header
        initial={false}
        animate={{ backgroundColor: isOpen ? "#403e3e" : "#403e3e" }}
        onClick={() => setExpanded(idx)}>
        <div className='title'>
          <p>{title}</p>
        </div>
      </motion.header>
      <AnimatePresence initial={false}>
        {isOpen && (
          <motion.section
            key="content"
            initial="collapsed"
            animate="open"
            exit="collapsed"
            variants={{
              open: { opacity: 1, height: "auto" },
              collapsed: { opacity: 0, height: 0 }
            }}
            transition={{ duration: 0.8, ease: [0.04, 0.62, 0.23, 0.98] }}
          >
            {
              (() => {
                if (title === '指導教授 Principal Investiagtor') {
                  return <PrincipalInvestiagtorPage />
                } else {
                  return <PeopleList key={title} list={list} />
                }
              })()
            }

          </motion.section>
        )}
      </AnimatePresence>
    </Container>
  )
}

function Section(props) {

  return (
    <Container className='section'>
      <Row>
        <Col xs={12}>
          <div className='title'>
            <p>{props.title}</p>
          </div>
        </Col>
      </Row>
      <Row>
        {
          props.data.map((member) => (
            <Col xs={12} md={6} className='my-3' key={member.name}>
              <IntroductionCard
                image={member.image}
                name={member.name}
                text={member.description}
              />
            </Col>
          ))
        }
      </Row>
    </Container>
  )
}

function IntroductionCard(props) {
  return (
    <div className='introCard'>
      <div className='image'>
        <ImgBlock src={props.image} />
      </div>
      <div className='text'>
        <p className='name'>{props.name}</p>
        <p className='description'>{props.text}</p>
      </div>
    </div>
  )
}

function PeopleList(props) {

  return (
    <React.StrictMode>
      {props.list.map((p) => (
        <Section title={p.type} data={p.data} key={p.title} />
      ))}
    </React.StrictMode>
  );
}





