import * as React from "react";
import { motion } from "framer-motion";



export const MenuToggle = ({ toggle, color, open }) => {
  const Path = props => (
    <motion.path
      fill="transparent"
      strokeWidth="3"
      stroke={color ? '#f0f0f0' : '#403e3e'}
      strokeLinecap="round"
      {...props}
    />
  );

  if (open) {
    return (
      <button onClick={toggle}>
        <svg width="23" height="20" viewBox="0 0 23 23" >
          <Path d="M 3 16.5 L 17 2.5" />
          <Path d="M 3 2.5 L 17 16.346" />
        </svg>
      </button>
    )
  } else {
    return (
      <button onClick={toggle}>
        <svg width="23" height="20" viewBox="0 0 23 23" >
          <Path d="M 2 2.5 L 20 2.5" />
          <Path
            d="M 2 9.423 L 20 9.423"
            transition={{ duration: 0.1 }}
          />
          <Path d="M 2 16.346 L 20 16.346" />
        </svg>
      </button>
    )
  }


}