import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { AiOutlineCopyrightCircle } from "react-icons/ai";
import ncku_logo from '../../img/ncku_logo_1.jpg'
import ncku_me_logo from '../../img/nckume_logo.png'

function Footer(props) {
  return (
    <div className="footer">
      <Container fluid className='w-100'>
        <Row className='w-100'>   
          <Col xs={6}>
            <a href='https://web.ncku.edu.tw/' target="_blank" className='imgbox'>
              <img src={ncku_logo} alt='ncku_logo' />
            </a>            
          </Col>
          <Col xs={6}>
            <a href='http://www.me.ncku.edu.tw/' target="_blank" className='imgbox'>
              <img src={ncku_me_logo} alt='ncku_me_logo' />
            </a>            
          </Col>
        </Row>
        <Row className='w-100'>
          <Col xs={12} className='copyright'>
            <p className='text-center m-0 py-2'>Copyright <AiOutlineCopyrightCircle /> 2021 Neural Computation Laboratory</p>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export { Footer };
