import React, { useState, useEffect } from "react";
import { Row, Col, Button, Form } from "react-bootstrap";
import { useHistory } from "react-router-dom";


import DragList from './dragList'
import { ImgBlock } from "../../components/imgBlock";
import apis from "../../api";
import { SelectBtn } from '../../components/selectbtn';


export default function Publication(props) {


  const [data, setData] = useState([]);
  const [loaded, setLoaded] = useState(false);
  const [select, setSelect] = useState("");
  const [file, setFile] = useState([]);

  let history = useHistory();

  const pagename = props.page
  const pagelist = props.list

  /*****************************************************************************************/
  // 自訂
  const [type, setType] = useState('')
  const [typeList, setTypeList] = useState([])
  const [typeStartIndex, setTypeStartIndex] = useState({})
  const [filteredData, setFilteredData] = useState([])
  const getTypeList = (fulldata) => {
    let tmp = []
    let tmpIndex = {}
    fulldata.forEach((item, index) => {
      if (!tmp.includes(item.type)) {
        tmp.push(item.type)
        tmpIndex = { ...tmpIndex, [item.type]: index }
      }
    })
    setTypeList(tmp)
    setTypeStartIndex(tmpIndex)
  }
  const getFilteredData = (fulldata, selectType) => {
    
    let tmp = []
    fulldata.forEach(d => { if (d.type === selectType) tmp.push(d) })
    setFilteredData(tmp)    
    setSelect(tmp[0])
  }
  useEffect(() => {
    if (type === '') return
    
    getFilteredData(data, type)
    
  }, [type, data])
  /*****************************************************************************************/

  useEffect(() => {
    if (!loaded) {
      apis
        .getDataList(pagename)
        .then((res) => {
          setSelect(res.data[0])
          setType(res.data[0].type)
          setData(res.data);

          getTypeList(res.data)
          getFilteredData(res.data, res.data[0].type)

          setLoaded(true);
        })
        .catch((error) => {
          history.push("/login");
          return;
        });
    }
  }, [loaded]);

  /** FUNC */
  const delete_btn = () => {
    let { uuid, ...payload } = select

    // 攔截
    if (uuid === '' || uuid === undefined) return

    apis
      .delData(pagename, uuid)
      .then((res) => { setData(res.data); })
      .catch((error) => { history.push("/login"); });
  };

  const clear = () => {
    let clear_select = {}
    Object.keys(select).forEach(key => {
      clear_select[key] = ''
    })
    setSelect(clear_select)
    setFile([]);
  };

  const modify_btn = async (e) => {
    let { uuid, ...payload } = select

    // 攔截
    if (uuid === '' || uuid === undefined) return

    if (file.length > 0) {
      const formData = new FormData();
      formData.append("file", file[0]);

      let res = await apis
        .fileUpload(formData)
        .catch((error) => {
          history.push("/login");
        });

      payload.image =
        "https://nclab-me-ncku.github.io/photo/" + res.data.filename;
    }

    apis
      .modifyData(pagename, payload, e.uuid)
      .then((res) => {
        setData(res.data);
        alert('修改成功')
        clear();
      })
      .catch((error) => {
        history.push("/login");
      });


  };

  const add_btn = async (e) => {
    let { uuid, ...payload } = select

    // 攔截
    if (uuid === undefined) return

    /* UPLOAD */
    if (file.length > 0) {
      const formData = new FormData();
      formData.append("file", file[0]);

      let res = await apis
        .fileUpload(formData)
        .catch((error) => { history.push("/login"); });

      payload.image =
        "https://nclab-me-ncku.github.io/photo/" + res.data.filename;
    }
    apis
      .addData(pagename, payload, e.uuid)
      .then((res) => {
        setData(res.data);
        alert('新增成功')
        clear();
      })
      .catch((error) => { history.push("/login"); });


  };

  const handle_swap = (s_idx, d_idx) => {
    
    s_idx += typeStartIndex[type]
    d_idx += typeStartIndex[type]
    
    let arr = Array.from(data);
    const [remove] = arr.splice(s_idx, 1);
    arr.splice(d_idx, 0, remove);
    setData(arr)

    apis
      .swap(pagename, s_idx, d_idx)
      .then(res => { setData(res.data) })
      .catch((error) => { history.push("/login"); });
  }

  const handle_click = (uuid) => {
    clear()
    for (let i = 0; i < data.length; i++)
      if (data[i]['uuid'] === uuid) {
        setSelect(data[i])
        break
      }
  }

  const handleChange = (event) => {

    let fieldName = event.target.name;
    let fleldVal = event.target.value;

    if (fieldName === "image_upload") {
      fleldVal = event.target.files;
      setFile(fleldVal);
    } else {
      setSelect({ ...select, [fieldName]: fleldVal });
    }

  };

  return (
    <React.StrictMode>
      <div className='btnArea'>
        <SelectBtn name={'頁面'} select={pagename} list={pagelist} onChange={(e) => history.push('/manage/' + e)} />
        <SelectBtn name={'type'} select={type} list={typeList} onChange={(e) => setType(e)} />
      </div>
      <div className="inner">
        <div className="table">
          <DragList data={filteredData} column={'title'} subColumn={'type'} onSwap={(s_idx, d_idx) => handle_swap(s_idx, d_idx)} onClick={(uuid) => handle_click(uuid)} />
        </div>
        <div className="form">
          <div className='from-area'>
            <p>內容</p>
            <Form className="m-5">
              <Row className="my-3">
                <Col>
                  <Form.Label>UUID</Form.Label>
                  <Form.Control
                    name="uuid"
                    type="text"
                    value={select.uuid}
                    readOnly
                  />
                </Col>

                <Col>
                  <Form.Label>形式</Form.Label>
                  <Form.Control
                    name="type"
                    as="select"
                    value={select.type}
                    onChange={handleChange}
                  >
                    <option>期刊論文</option>
                    <option>會議論文</option>
                    <option>專書論文</option>
                  </Form.Control>
                </Col>
              </Row>
              <Row className="my-3">
                <Col>
                  <Form.Label>論文名稱</Form.Label>
                  <Form.Control
                    name="title"
                    type="text"
                    value={select.title}
                    onChange={handleChange}
                  />
                </Col>
              </Row>
              <Row className="my-3">
                <Col>
                  <Form.Label>作者</Form.Label>
                  <Form.Control
                    name="author"
                    type="text"
                    value={select.author}
                    onChange={handleChange}
                  />
                </Col>
              </Row>
              <Row className="my-3">
                <Col>
                  <Form.Label>出處</Form.Label>
                  <Form.Control
                    name="at"
                    type="text"
                    value={select.at}
                    onChange={handleChange}
                  />
                </Col>
              </Row>
              <Row className="my-3">
                <Col>
                  <Form.Label>時間</Form.Label>
                  <Form.Control
                    name="time"
                    type="text"
                    value={select.time}
                    onChange={handleChange}
                    readOnly={select.type !== "專書論文"}
                  />
                </Col>
                <Col>
                  <Form.Label>頁數</Form.Label>
                  <Form.Control
                    name="numberOfPages"
                    type="text"
                    value={select.numberOfPages}
                    onChange={handleChange}
                    readOnly={select.type !== "專書論文"}
                  />
                </Col>
              </Row>
              <Row className={(file.length > 0) ? 'my-3' : 'my-3 onSelect'}>
                <Col xs={12} md={3} className='my-1'>
                  <ImgBlock src={select.image} />
                </Col>
                <Col xs={12} md={9} className='my-1'>
                  <Form.Label>圖片網址</Form.Label>
                  <Form.Control
                    name="image"
                    type="text"
                    value={select.image}
                    onChange={handleChange}
                    readOnly={(file.length > 0)}
                  />
                  <Form.Text style={{ color: 'red' }}>{(file.length > 0) ? '' : '將使用該圖片'} </Form.Text>
                </Col>
              </Row>
              <Row className={(file.length === 0) ? 'my-3' : 'my-3 onSelect'}>
                <Col xs={12} md={3} className='my-1'>
                  <ImgBlock src={(file.length > 0) ? URL.createObjectURL(file[0]) : ''} />
                </Col>
                <Col xs={12} md={9} className='my-1'>
                  <Form.Label>圖片上傳</Form.Label>
                  <Form.File
                    key={select.uuid}
                    name="image_upload"
                    onChange={handleChange}
                  />
                  <Form.Text>若要取消選取，請點選進入選擇檔案介面後再按取消</Form.Text>
                  <Form.Text style={{ color: 'red' }}>{(file.length === 0) ? '' : '將使用該圖片'} </Form.Text>
                </Col>
              </Row>

              <Row className="mt-5 mb-1">
                <Col>
                  <Button
                    className="w-100"
                    variant="secondary"
                    onClick={() => modify_btn(select)}
                  >
                    修改
                </Button>
                </Col>
                <Col>
                  <Button
                    className="w-100"
                    variant="danger"
                    onClick={() => delete_btn(select.uuid)}
                  >
                    刪除
                </Button>
                </Col>
              </Row>
              <Row className="mt-1">
                <Col>
                  <Button
                    className="w-100"
                    variant="info"
                    onClick={() => add_btn(select)}
                  >
                    新增
                </Button>
                </Col>
                <Col>
                  <Button
                    className="w-100"
                    variant="warning"
                    onClick={() => clear()}
                  >
                    清除
                </Button>
                </Col>
              </Row>
            </Form>
          </div>
        </div>
      </div>
    </React.StrictMode>

  );
}