import React, { useState, useEffect } from "react";
import { readRemoteFile } from "react-papaparse";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import { Container, Row, Col, Button } from "react-bootstrap";
import Loader from 'react-loader-spinner'
import { motion, AnimatePresence } from "framer-motion";
import { ImgBlock } from '../../components/imgBlock'

function Publications(params) {
  const [paperData, setPaperData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vToFBGxADSvoEhR4F-nirqgFxzeQyMc0l6f-2aWzkIX_5DrtmeVGug8pRwXF8E5XKJ36Q5LV1HQxwGf/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          setPaperData(results.data);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '發表', en: 'Publications' }} />
        <div className='publication'>
          <PublicPage data={paperData} />
        </div>
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '發表', en: 'Publications' }} />
        <div className='publication'>
          <div className='text-center py-5'>
            <Loader type="Grid" color="#403E3E" height={80} width={80} />
          </div>
        </div>
        <Footer />
      </React.StrictMode>
    )
  }

}

export { Publications };

function SelectBtn(props) {
  return (
    <Container>
      <Row>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("papers");
            }}
          >
            <p className='my-2'>期刊論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("proceedings");
            }}
          >
            <p className='my-2'>會議論文</p>
          </Button>
        </Col>
        <Col xs={4}>
          <Button
            variant="outline-dark"
            className="w-100"
            onClick={() => {
              props.selectChange("bookPaper");
            }}
          >
            <p className='my-2'>專書論文</p>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

function PublicationsList(props) {
  return (
    <Container>
      <Row>
        {props.list.map((p) => (
          <Col xs={12}>
            <PublicationsCard title={p.title} author={p.author} at={p.at} image={p.image} />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

function PublicationsCard(props) {
  return (
    <React.StrictMode>
      <a
        href={"https://scholar.google.com/scholar?hl=zh-TW&q=" + props.title}
        target="_blank"
        style={{ textDecoration: "none" }}
      >
        <div className="publications-card">
          <div className='image_area'>
            <ImgBlock src={props.image} />
          </div>
          <div className='text_area'>
            <div className="title">
              <p className="m-0">{props.title}</p>
            </div>
            <div className="at">
              <p className="m-0">{props.at}</p>
            </div>
            <div className="author">
              <p className="m-0">{props.author}</p>
            </div>
          </div>

        </div>
      </a>
      <hr />
    </React.StrictMode>
  );
}

function PublicPage(props) {
  const [paper, setPaper] = useState([])
  const [loaded, setLoaded] = useState(false)
  const [expanded, setExpanded] = useState(0)

  useEffect(() => {

    const processData = () => {
      let tmp_paperdata = {}


      for (let index = 1; index < props.data.length; index++) {
        // [type] create if not exist
        if (!(props.data[index][1] in tmp_paperdata)) {
          tmp_paperdata[props.data[index][1]] = []
        }

        // add data
        tmp_paperdata[props.data[index][1]].push({
          title: props.data[index][2],
          author: props.data[index][3],
          at: props.data[index][4],
          rank: props.data[index][5],
          image: props.data[index][8],
        })

      }

      const dict2list = (type) => {
        let list = []
        tmp_paperdata[type].forEach(p => {
          list.push(p)
        })
        return list
      }

      let paperdata = []

      Object.keys(tmp_paperdata).forEach(typeName => {
        paperdata.push({
          type: typeName,
          data: dict2list(typeName)
        })
      })

      setPaper(paperdata)
      setLoaded(true)
    }

    if (!loaded) processData()

  }, [loaded]);


  // render
  if (loaded) {
    const accordionIds = [...Array(paper.length).keys()];
    return accordionIds.map((i) => (
      <Accordion key={i} idx={i} expanded={expanded} setExpanded={setExpanded} list={paper[i]['data']} title={paper[i]['type']} />
    ));
  } else {
    return (
      <div>

      </div>
    )
  }

}

function Accordion({ idx, expanded, setExpanded, list, title }) {
  const isOpen = (idx === expanded);

  // By using `AnimatePresence` to mount and unmount the contents, we can animate
  // them in and out while also only rendering the contents of open accordions
  return (
    <Container>
      <motion.header
        initial={false}
        animate={{ backgroundColor: isOpen ? "#403e3e" : "#403e3e" }}
        onClick={() => setExpanded(idx)}>
        <div className='title'>
          <p>{title}</p>
        </div>
      </motion.header>
      <AnimatePresence initial={false}>
        {isOpen && (
          <motion.section
            key="content"
            initial="collapsed"
            animate="open"
            exit="collapsed"
            variants={{
              open: { opacity: 1, height: "auto" },
              collapsed: { opacity: 0, height: 0 }
            }}
            transition={{ duration: 0.8, ease: [0.04, 0.62, 0.23, 0.98] }}
          >
            <PublicationsList list={list} />

          </motion.section>
        )}
      </AnimatePresence>
    </Container>
  )
}