import React, { useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import { ImgBlock } from '../../components/imgBlock';
import Loader from 'react-loader-spinner'
import { readRemoteFile } from "react-papaparse";
import { Container, Row, Col } from "react-bootstrap";



function Honors(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vSwX0V1a8d8tiWuc5S5HCnB0z7PfyTLYJ-Gs3svoAtS0WQqVzidUELm0WDIqhbvUdPxFpTrWD3tVTsc/pub?output=csv';
      readRemoteFile(url, {
        complete: (results) => {

          var newData = []
          for (let i = 1; i < results.data.length; i++) {


            newData.push({
              year: results.data[i][1],
              title: results.data[i][2],
              award: results.data[i][3],
              image: results.data[i][4],
            })
          }



          setCsvData(newData);
          setGetData(true);
        },
      });

    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '獎項', en: 'Honor' }} />
        <div className='honor'>
          <ResearchSection data={csvData} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '獎項', en: 'Honor' }} />
        <div className='honor'>
          <div className='text-center py-5'>
            <Loader type="Grid" color="#403E3E" height={80} width={80} />
          </div>
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}



export { Honors };



function Section(props) {


  return (
    <div className='section'>
      <Container className='p-5'>
        <Row>
          <Col xs={12} md={12}>
            <p className='year'>{props.year}</p>
            <hr />
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={8}>
            <p className='title'>{props.title}</p>
            <p className='award'>{props.award}</p>
          </Col>
          <Col xs={12} md={4}>
            <ImgBlock src={props.image} />
          </Col>
        </Row>
      </Container>
      <div className='time-line'>

      </div>
      <div className='time-circle'></div>
    </div>

  )
}

function ResearchSection(props) {
  let data = props.data

  return (
    data.map((d) => (
      <Section year={d.year} title={d.title} award={d.award} image={d.image} />
    ))
  )
}