import React from "react";

function ImgBlock(props) {
  return (
    <div
      style={{
        width: "100%",
        paddingBottom: "100%",
        height: "0",
        position: "relative",
      }}
    >
      <div
        style={{
          position: "absolute",
          top: "0",
          bottom: "0",
          right: "0",
          left: "0",
        }}
      >
        <div
          style={{
            height: "100%",
            overflow: "hidden",
            display: "flex",
            justifyContent: "center",
          }}
        >
          <img style={{ height: "100%" }} alt={props.alt} src={props.src} />
        </div>
      </div>
    </div>
  );
}

export { ImgBlock };
