import React, { Component, useEffect, useState } from "react";
import { Container, Row, Col, Button } from "react-bootstrap";
import { WebNav } from "../../components/navbar";
import { Footer } from "../../components/footer";
import {
  BsChevronDoubleDown,
  BsFillInfoSquareFill,
  BsChatSquareDotsFill,
} from "react-icons/bs";
import { AiOutlineCaretRight } from "react-icons/ai";
import { readRemoteFile } from "react-papaparse";
import Particles from "react-tsparticles";
import { ImgBlock } from "../../components/imgBlock";
import { useHistory } from "react-router-dom";

/** image */
import nn_model from "../../img/nn_model.svg";
import bmiImg from "../../img/bmi.webp";
import LFP_raw from "../../img/raw_LFP.png";
import spike_raw from "../../img/raw_spike.png";
import LFP_process from "../../img/process_LFP.png";
import spike_process from "../../img/process_spike.png";
// import spike_f0f0f0 from '../../img/spike_f0f0f0.svg'
// import spike_5899E2 from '../../img/spike_5899E2.svg'
// import spike_ffa400 from '../../img/spike_ffa400.svg'
// import spike_403e3e from '../../img/spike_403e3e.svg'
import spike from "../../img/spike.svg";
import ncku_logo from "../../img/ncku_logo.png";

function Home(props) {
  return (
    <React.StrictMode>
      <WebNav />
      <div className="home">
        <Cover />
        <JoinUs />
        <Purpose />
        <Needed />
        <Papers />
        <Honors />
        <Researchs />
      </div>
      <Footer />
    </React.StrictMode>
  );
}

export { Home };
/** -------------------------------------------------------------------------------------------- */

function Cover(props) {
  return (
    <div className="cover">
      <div className="background">
        <img src={spike} />
      </div>
      <div className="title">
        <div className="ncku">
          <p>NCKU</p>
          <img src={ncku_logo} />
        </div>
        <div className="nclab">
          <p>Neural</p>
          <p style={{ color: "#ffa400" }}>Computation</p>
          <p>Laboratory</p>
        </div>
      </div>
    </div>
  );
}
/** -------------------------------------------------------------------------------------------- */
function JoinUs(props) {
  return (
    <div className="joinUs">
      <Container>
        <Row className="justify-content-md-center">
          <Col xs={12} md={6}>
            <a
              target="_blank"
              href="https://docs.google.com/forms/d/e/1FAIpQLSddVTVVC7N0iLtpVYTZs3vcPknwJzIXBcXCjtT_mhQPcZY-tw/viewform"
              style={{ textDecoration: "None" }}
            >
              <div className="btn_card">
                <div className="icon">
                  <BsChatSquareDotsFill />
                </div>
                <p>面試報名</p>
              </div>
            </a>
          </Col>
          <Col xs={12} md={6}>
            <a href="/#/contact" style={{ textDecoration: "None" }}>
              <div className="btn_card">
                <div className="icon">
                  <BsFillInfoSquareFill />
                </div>
                <p>聯絡我們</p>
              </div>
            </a>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

/** -------------------------------------------------------------------------------------------- */
function Papers(props) {
  const [getData, setGetData] = useState(false);
  const [showData, setShowData] = useState([]);

  const history = useHistory();

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vToFBGxADSvoEhR4F-nirqgFxzeQyMc0l6f-2aWzkIX_5DrtmeVGug8pRwXF8E5XKJ36Q5LV1HQxwGf/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          setShowData(results.data.slice(1, 4));
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <div className="papers">
        <Container>
          <Row>
            <Col
              xs={12}
              md={5}
              style={{ display: "flex", alignItems: "center" }}
            >
              <div className="text">
                <p className="subtitle">發表</p>
                <p className="title">論文</p>
                <p className="info">目前所發表的論文，包含期刊以及研討會</p>
                <p
                  className="more"
                  onClick={() => history.push("/publications")}
                >
                  more...
                </p>
              </div>
            </Col>
            <Col xs={12} md={7}>
              <div className="list">
                {showData.map((paper) => (
                  <Paperblock
                    key={paper[0]}
                    image={paper[8]}
                    title={paper[2]}
                    at={paper[4]}
                    author={paper[3]}
                  />
                ))}
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  } else {
    return <div></div>;
  }
}

function Paperblock(props) {
  return (
    <div data-aos="fade-up" data-aos-anchor-placement="center-bottom">
      <div className="paperblock">
        <div className="paperimage">
          <ImgBlock src={props.image} />
        </div>
        <div className="papertitle">
          <p>{props.title}</p>
        </div>
      </div>
    </div>
  );
}
/** -------------------------------------------------------------------------------------------- */

function Honors(props) {
  const [getData, setGetData] = useState(false);
  const [showData, setShowData] = useState([]);

  const history = useHistory();

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vSwX0V1a8d8tiWuc5S5HCnB0z7PfyTLYJ-Gs3svoAtS0WQqVzidUELm0WDIqhbvUdPxFpTrWD3tVTsc/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          setShowData(results.data.slice(1, 5));
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <div className="honors">
        <Container>
          <Row>
            <Col>
              <div className="text">
                <p className="title">獎項</p>
              </div>
            </Col>
          </Row>
          <Row className="list">
            {showData.map((honor) => (
              <Honorblock
                key={honor[0]}
                image={honor[4]}
                time={honor[1]}
                title={honor[2]}
                award={honor[3]}
              />
            ))}
          </Row>
          <Row>
            <Col>
              <div className="text">
                <p className="more" onClick={() => history.push("/honors")}>
                  more...
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  } else {
    return <div></div>;
  }
}

function Honorblock(props) {
  return (
    <Col>
      <div data-aos="fade-right">
        <div className="honorblock">
          <div className="honorimage">
            <ImgBlock src={props.image} />
          </div>
          <div className="honortitle">
            <p>{props.title}</p>
          </div>
        </div>
      </div>
    </Col>
  );
}
/** -------------------------------------------------------------------------------------------- */

function Researchs(props) {
  const [getData, setGetData] = useState(false);
  const [showData, setShowData] = useState([]);

  const history = useHistory();

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vQZ-hyllMx64mYoxETybRRDNpWqoNcv5N2eKVKqX_1tiXLUIPArzlhcdR1H8-PHMFNByRnu51nuiKhU/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          setShowData(results.data.slice(1, 4));
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <div className="researchs">
        <Container>
          <Row>
            <Col xs={{ span: 12, order: 'last' }} md={{ span: 7, order: 'first' }}>
              <div className="list">
                {showData.map((research) => (
                  <Researchblock
                    key={research[0]}
                    image={research[8]}
                    title={research[1]}
                  />
                ))}
              </div>
            </Col>
            <Col
              xs={12}
              md={5}
              style={{ display: "flex", alignItems: "center" }}
            >
              <div className="text">
                <p className="subtitle">成果</p>
                <p className="title">研究</p>
                <p className="info"></p>
                <p className="more" onClick={() => history.push("/research")}>
                  more...
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  } else {
    return <div></div>;
  }
}

function Researchblock(props) {
  return (
    <div data-aos="fade-up" data-aos-anchor-placement="center-bottom">
      <div className="researchblock">
        <div className="researchimage">
          <ImgBlock src={props.image} />
        </div>
        <div className="researchtitle">
          <p>{props.title}</p>
        </div>
      </div>
    </div>
  );
}
/** -------------------------------------------------------------------------------------------- */

function Purpose(props) {
  return (
    <div className="purpose">
      <Container>
        <Row>
          <Col
            xs={12}
            md={12}
            style={{ display: "flex", alignItems: "center" }}
          >
            <div className="text">
              <p className="subtitle">研究目標</p>
              <p className="title">神經解碼</p>
              <p className="info">
                本實驗室研究猴子與大鼠的神經訊號，並以類神經網路為基礎，設計侵入式腦機介面，幫助患者以意念控制外部機器。
              </p>
            </div>
          </Col>
        </Row>
        <Row data-aos="fade-up" data-aos-anchor-placement="center-bottom">
          <Col xs={12} md={{ span: 4, offset: 1 }}>
            <ImgBlock src={nn_model} />
          </Col>
          <Col
            xs={12}
            md={{ span: 5, offset: 1 }}
            style={{ display: "flex", alignItems: "center" }}
          >
            <div className="subtext">
              <p className="title">類神經網路</p>
              <p className="subtitle">Neural Network</p>
              <p className="info">由資料中歸納出特徵的方法</p>
              <a
                href="https://www.google.com/search?q=neural+network"
                target="blank"
                style={{ textDecoration: "None" }}
              >
                <p className="more">深入了解</p>
              </a>
            </div>
          </Col>
        </Row>
        <Row data-aos="fade-up" data-aos-anchor-placement="center-bottom">
          <Col xs={12} md={{ span: 4, offset: 1 }}>
            <ImgBlock src={bmiImg} />
          </Col>
          <Col
            xs={12}
            md={{ span: 5, offset: 1 }}
            style={{ display: "flex", alignItems: "center" }}
          >
            <div className="subtext">
              <p className="title">侵入式腦機介面</p>
              <p className="subtitle">Invasive Brain Machine Interface</p>
              <p className="info">藉由將晶片植入大腦皮層以取得訊號</p>
              <a
                href="https://www.google.com/search?q=Invasive+Brain+Machine+Interface"
                target="blank"
                style={{ textDecoration: "None" }}
              >
                <p className="more">深入了解</p>
              </a>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}
/** -------------------------------------------------------------------------------------------- */

function Needed(props) {
  return (
    <div className="needed">
      <Container>
        <Row>
          <Col
            xs={12}
            md={12}
            style={{ display: "flex", alignItems: "center" }}
          >
            <div className="text">
              <p className="title">訊號處理</p>
              <p className="subtitle">Signal Process</p>
              <p className="info"></p>
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={12}>
            <p className="subtext"></p>
          </Col>
        </Row>
        <Row>
          <Col xs={4} data-aos="fade-right" data-aos-delay={0}>
            <ImgBlock src={LFP_raw} />
          </Col>
          <Col
            xs={4}
            data-aos="fade-right"
            data-aos-delay={100}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <p className="arrow">
              <AiOutlineCaretRight />
            </p>
          </Col>
          <Col xs={4} data-aos="fade-right" data-aos-delay={200}>
            <ImgBlock src={LFP_process} />
          </Col>
        </Row>
        <Row>
          <Col xs={12} md={12}>
            <p className="subtext"></p>
          </Col>
        </Row>
        <Row>
          <Col xs={4} data-aos="fade-right" data-aos-delay={0}>
            <ImgBlock src={spike_raw} />
          </Col>
          <Col
            xs={4}
            data-aos="fade-right"
            data-aos-delay={100}
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <p className="arrow">
              <AiOutlineCaretRight />
            </p>
          </Col>
          <Col xs={4} data-aos="fade-right" data-aos-delay={200}>
            <ImgBlock src={spike_process} />
          </Col>
        </Row>
      </Container>
    </div>
  );
}
