// pacakge
import React, { useState, useEffect } from "react";
import { Col, Container, Row } from "react-bootstrap";
import Loader from "react-loader-spinner";
import { readRemoteFile } from "react-papaparse";
import { Switch, Route, useParams, useHistory } from "react-router-dom";

// component
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from "../../components/footer";
import { ImgBlock } from '../../components/imgBlock'



function Research(params) {
  const [csvData, setCsvData] = useState('');
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url =
        "https://docs.google.com/spreadsheets/d/e/2PACX-1vQZ-hyllMx64mYoxETybRRDNpWqoNcv5N2eKVKqX_1tiXLUIPArzlhcdR1H8-PHMFNByRnu51nuiKhU/pub?output=csv";
      readRemoteFile(url, {
        complete: (results) => {
          // console.log("Results:", results);
          var newCsvData = [];
          for (let i = 1; i < results.data.length; i++) {
            var data = {};
            for (let j = 1; j < results.data[0].length; j++) {
              data[results.data[0][j]] = results.data[i][j];
            }
            newCsvData.push(data);
          }
          // console.log(newCsvData);
          setCsvData(newCsvData);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '研究', en: 'Research' }} />
        <div className="research">
          <ResearchSection data={csvData} />
        </div>
        <Footer />
      </React.StrictMode>
    );
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '研究', en: 'Research' }} />
        <div className="research">
          <div className="text-center py-5">
            <Loader type="Grid" color="#403E3E" height={80} width={80} />
          </div>
        </div>
        <Footer />
      </React.StrictMode>
    );
  }
}

export { Research };

function ResearchSection(props) {
  return (
    <Switch>
      <Route exact path="/research" children={
        <Container fluid style={{ width: '80%' }} >
          <Row>
            {props.data.map((d) => (
              <IntroCard key={d.projectName} image={d.image} projectName={d.projectName} />
            ))}
          </Row>
        </Container>
      } />
      <Route path="/research/:selectProject" children={<IntroductionPage rawData={props.data} />} />
    </Switch>
  )

}

function IntroductionPage(props) {

  let { selectProject } = useParams();
  const history = useHistory();
  const [loaded, setLoaded] = useState(false)
  const [project, setProject] = useState({})
  const [sectionData, setSectionData] = useState([])

  let rawData = props.rawData
  useEffect(() => {
    if (!loaded) {
      let newData = {
        projectName: selectProject,
        publish: "",
        purpose: "",
        challenge: "",
        method: "",
        result: "",
        application: "",
        image: "",
        imageDescription: "",
      }
      for (let i = 0; i < rawData.length; i++) {
        if (rawData[i].projectName === selectProject) {
          newData.publish = rawData[i].publish;
          newData.purpose = rawData[i].purpose;
          newData.challenge = rawData[i].challenge;
          newData.method = rawData[i].method;
          newData.result = rawData[i].result;
          newData.application = rawData[i].application;
          newData.image = rawData[i].image;
          newData.imageDescription = rawData[i].imageDescription;

          break
        }

        // 找不到
        if (i >= rawData.length - 1) {
          history.push('/research')
        }
      }

      let data = []
      if (newData.publish !== "") data.push({ title: '發表', text: newData.publish })
      if (newData.purpose !== "") data.push({ title: '目的', text: newData.purpose })
      if (newData.challenge !== "") data.push({ title: '挑戰', text: newData.challenge })
      if (newData.method !== "") data.push({ title: '方法', text: newData.method })
      if (newData.result !== "") data.push({ title: '結果', text: newData.result })
      if (newData.application !== "") data.push({ title: '應用', text: newData.application })

      setProject(newData)
      setSectionData(data)

      setLoaded(true)
    }
  }, [loaded])

  if (loaded) {
    return (
      <div className="introPage">
        <Container>
          <Row>
            <Col xs={12}>
              <div className="projectName">
                <p>{project.projectName}</p>
              </div>
            </Col>
          </Row>
          {
            sectionData.map(d => (
              <Row className="justify-content-md-center">
                <Col xs={12} md={10}>
                  <div className="description">
                    <p className="title">{d.title}</p>
                    <p className="text">{d.text}</p>
                  </div>
                </Col>
              </Row>
            ))
          }
          <Row className="justify-content-md-center">
            <Col xs={12} md={10}>
              <div className="image">
                <p className="title">示意圖</p>
                <img src={project.image} />
                <p className="text">{project.imageDescription}</p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    );
  } else {
    return (
      <div>

      </div>
    )
  }

}

function IntroCard(props) {
  const history = useHistory();

  return (
    <Col xs={12} md={6}>
      <div className='introCard' onClick={() => { history.push('/research/' + props.projectName) }}>
        <div className='title'>
          <p>{props.projectName}</p>
        </div>
        <div className='image'>
          <ImgBlock alt={props.projectName} src={props.image} />
        </div>
      </div>
    </Col>

  )
}