import axios from 'axios'
import param from '../param'


const api = axios.create({
    baseURL: param.apiUrl,
})



export const getDataList = (sheetname) => api.get('/api', { params: { name: sheetname }, headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const delData = (sheetname, uuid) => api.delete('/api', { params: { name: sheetname }, data: { uuid: uuid }, headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const modifyData = (sheetname, payload, uuid) => api.put('/api', { data: payload, uuid: uuid }, { params: { name: sheetname }, headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const addData = (sheetname, payload) => api.post('/api', { data: payload }, { params: { name: sheetname }, headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const swap = (sheetname, source_index, destination_index) => api.post('/swap', { src_index: source_index, des_index: destination_index }, { params: { name: sheetname }, headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const fileUpload = (payload) => api.post('/file', payload, { headers: { Authorization: 'Bearer ' + localStorage.getItem("token") } })
export const login = (username, password) => api.post('/login', { username: username, password: password })

const apis = {
    getDataList, delData, modifyData, addData, fileUpload, login, swap
}

export default apis