import React, { } from "react";
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';


export default function DragList(params) {
    let column = params.column
    let subColumn = params.subColumn


    return (
        <DragDropContext
            onDragEnd={result => {
                const { source, destination, draggableId } = result;
                if (!destination) {
                    return;
                }
                params.onSwap(source.index, destination.index)
            }}
        >
            <Droppable droppableId={'d'}>
                {
                    provided => (
                        <div className='dragList'
                            ref={provided.innerRef}
                            {...provided.droppableProps}
                        >
                            <p>項目表</p>
                            <p style={{ fontSize: '0.8rem', color: 'red' }}>拖曳可改變順序</p>
                            {
                                params.data.map((d, i) => (
                                    <Card key={d.uuid} uuid={d.uuid} index={i} text={d[column]} subText={(subColumn === undefined) ? '' : d[subColumn]}
                                        onClick={(e) => params.onClick(e)} />
                                ))
                            }
                        </div>
                    )
                }
            </Droppable>
        </DragDropContext>
    )

}

function Card(params) {

    return (
        <Draggable
            draggableId={params.uuid}
            index={params.index}
            key={params.uuid}
        >
            {p => (
                <div
                    ref={p.innerRef}
                    {...p.draggableProps}
                    {...p.dragHandleProps}
                    key={params.uuid}
                >
                    <div className='dragCard' onClick={() => params.onClick(params.uuid)}>
                        <p className='subtitle'>{params.subText}</p>
                        <p className='text'>{params.text}</p>
                    </div>

                </div>
            )}
        </Draggable>
    )
}