import React, { useState, useEffect } from "react";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import Loader from 'react-loader-spinner'
import { Container, Row, Col } from "react-bootstrap";
// import photoData from "./data";
import { readRemoteFile } from "react-papaparse";

function Photo(params) {
  const [csvData, setCsvData] = useState([]);
  const [getData, setGetData] = useState(false);

  useEffect(() => {
    if (!getData) {
      const url = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vQkN8n2hkawOqmiAh0oAQzwEPLAOhU5oesfpovSr5oolxHOrHKZ8pj2GvfO4b3LRVnXppr-9oIPrgsu/pub?output=csv';
      readRemoteFile(url, {
        complete: (results) => {

          var newPhotoData = []
          for (let i = 1; i < results.data.length; i++) {
            newPhotoData.push({
              image: results.data[i][2],
              text: results.data[i][1],
            })
          }
          setCsvData(newPhotoData);
          setGetData(true);
        },
      });
    }
  }, [getData]);

  if (getData) {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '照片', en: 'Photo' }} />
        <div className='photo'>
          <PhotoSection data={csvData} />
        </div>
        <Footer />
      </React.StrictMode>
    )
  } else {
    return (
      <React.StrictMode>
        <WebNav />
        <Cover pageName={{ tw: '照片', en: 'Photo' }} />
        <div className='photo'>
          <div className='text-center py-5'>
            <Loader type="Grid" color="#403E3E" height={80} width={80} />
          </div>
        </div>
        <Footer />
      </React.StrictMode>
    )
  }
}



export { Photo };

function PhotoSection(props) {
  let data = props.data
  return (
    <Container fluid className='py-3'>
      <Row>
        {
          data.map(d => (
            <Col xs={12} md={6} className='py-3' key={d.text}>
              <PhotoCard src={d.image} text={d.text} />
            </Col>

          ))
        }
      </Row>
    </Container>
  )
}

// 16:9
function PhotoCard(props) {
  return (
    <div className='card'>
      <div className='imagebox-1' >
        <div className='imagebox-2' >
          <div className='imagebox-3' >
            <img src={props.src} />
          </div>
        </div>
      </div>
      <div className='text'>
        <p>{props.text}</p>
      </div>
    </div>

  )
}