import React, { useEffect, useState } from "react";
import { Nav } from "react-bootstrap";
import { MenuToggle } from './MenuToggle'
import { motion } from "framer-motion";

// export default WebNav;
import logo from "../../img/logo.png";



function WebNav(props) {
    const [offset, setOffset] = useState(0)
    const [isTop, setIsTop] = useState(true)
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    const [mobile, setMobile] = useState(false)
    const [toggle, setToggle] = useState(false)

    // scroll
    useEffect(() => {
        function handleScroll() {
            setOffset(window.pageYOffset)
        }
        window.addEventListener("scroll", handleScroll)
        return () => {
            window.removeEventListener("scroll", handleScroll)
        }
    }, [])

    useEffect(() => {
        if (offset == 0) {
            setIsTop(true)
        } else {
            setIsTop(false)
        }
    }, [offset])

    // window resize
    function getWindowDimensions() {
        const { innerWidth: width, innerHeight: height } = window;
        return {
            width,
            height
        };
    }

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    useEffect(() => {
        if (windowDimensions.width < 600) {
            if (!mobile) setMobile(true);
        }
        else {
            if (mobile) setMobile(false);
        }

    }, [windowDimensions])

    // 
    const sidebar = {
        open: (height = windowDimensions.height) => ({
            clipPath: `circle(100vh at Calc(100vw ) 0rem)`,
            transition: {
                type: "spring",
                stiffness: 50,
                restDelta: 2
            }
        }),
        closed: (width = 1500) => ({
            clipPath: `circle(0rem at Calc(100vw ) 0rem)`,
            transition: {
                delay: 0.1,
                type: "spring",
                stiffness: 400,
                damping: 40
            }
        })
    };



    // render
    if (mobile) {

        return (
            <div className={isTop ? 'navbar-outer' : 'navbar-outer navMove'}>
                <div className='navbar-inner'>
                    <div className='navbarBrand'>
                        <a href='/#' style={{ textDecoration: 'none' }}>
                            <div className='navbarBrand-inner'>
                                <img className='brandImg' src={logo} />
                                <div style={{ display: 'block', width: '1rem' }} />
                                <p className='brandName'>Neural Computation Lab</p>
                            </div>
                        </a>
                    </div>
                </div>
                <motion.nav animate={toggle ? "open" : "closed"}   >
                    <motion.div className="background" variants={sidebar}>
                        <Nav className="" onClick={() => (setToggle(false))}>
                            <div style={{ width: '100%', height: '10rem' }} />
                            <Nav.Link href="/#/contact">聯絡資訊</Nav.Link>
                            <Nav.Link href="/#/honors">獎項</Nav.Link>
                            <Nav.Link href="/#/people">實驗室成員</Nav.Link>
                            <Nav.Link href="/#/publications">發表</Nav.Link>
                            <Nav.Link href="/#/research">研究</Nav.Link>
                            <Nav.Link href="/#/photo">相片</Nav.Link>
                        </Nav>
                    </motion.div>

                    <div className='toggle'>
                        <MenuToggle toggle={() => setToggle(!toggle)} color={(isTop && !toggle)} open={toggle} />
                    </div>

                </motion.nav>
            </div >
        )
    } else {
        return (
            <div className={(offset == 0) ? 'navbar-outer' : 'navbar-outer navMove'}>
                <div className='navbar-inner'>
                    <div className='navbarBrand'>
                        <a href='/#' style={{ textDecoration: 'none' }}>
                            <div className='navbarBrand-inner'>
                                <img className='brandImg' src={logo} />
                                <div style={{ display: 'block', width: '1rem' }} />
                                <p className='brandName'>Neural Computation Lab</p>
                            </div>
                        </a>
                    </div>

                    <Nav className="ml-auto mr-3">
                        <Nav.Link href="/#/contact">聯絡資訊</Nav.Link>
                        <Nav.Link href="/#/honors">獎項</Nav.Link>
                        <Nav.Link href="/#/people">實驗室成員</Nav.Link>
                        <Nav.Link href="/#/publications">發表</Nav.Link>
                        <Nav.Link href="/#/research">研究</Nav.Link>
                        <Nav.Link href="/#/photo">相片</Nav.Link>
                    </Nav>
                </div>

            </div >
        )
    }

}



export { WebNav }