import { IoCubeOutline } from "react-icons/io5";
import { Container, Row, Col } from "react-bootstrap";
import React, { useEffect, useState } from "react";

import spike from "../../img/spike.svg";
import ncku_logo from "../../img/ncku_logo.png";


function Cover(props) {
    // pageName={{ tw: '獎項', en: 'Honor' }}
    return (
        <div className='page'>
            <div className="cover">
                <div className="background">
                    <img src={spike} />
                </div>
                <div className="title">
                    <div className="ncku">
                        <p>NCKU</p>
                        <img src={ncku_logo} />
                    </div>
                    <div className="nclab">
                        <p>Neural</p>
                        <p style={{ color: "#ffa400" }}>Computation</p>
                        <p>Laboratory</p>
                    </div>
                    <div className='pagetitle'>
                        <p>{props.pageName.tw}</p>
                    </div>
                </div>

            </div>

        </div>
    )
}

export { Cover }