import React, { useState } from "react";
import { Modal } from "react-bootstrap";

function SelectBtn(param) {
  const [modalShow, setModelShow] = useState(false)

  const handleClose = () => setModelShow(false);
  const handleShow = () => setModelShow(true);
  const handleSelect = (name) => {
    setModelShow(false)
    param.onChange(name)
  }

  return (
    <React.StrictMode>
      <div className='pageSection'>
        <div className='title'>
          <p>{param.name}</p>
        </div>
        <div className='pageName' onClick={() => handleShow()}>
          <p>{param.select}</p>
        </div>
      </div>

      <Modal show={modalShow} onHide={handleClose} centered size="sm">
        <div className='pageSelectModal'>
          <div className='title'>
            <p>選擇{param.name}</p>
          </div>
          {
            param.list.map((p, i) => (
              <div key={i} className='item' onClick={() => handleSelect(p)}>
                <p>{p}</p>
              </div>
            ))
          }
        </div>
      </Modal>
    </React.StrictMode>


  )
}


export { SelectBtn }