import React, { useState } from "react";

import { WebNav } from "../../components/navbar";
import { Footer } from "../../components/footer";
import { Switch, Route, useParams, useHistory } from "react-router-dom";

import People from "./People";
import Photo from "./Photo";
import Research from "./Research";
import Honor from "./Honor";
import Publication from "./Publication";

function Manage() { 
  

  return (
    <React.StrictMode>
      <WebNav />
      <div className="manage">
        <div
          style={{ width: "100%", height: "5rem", backgroundColor: "#212020" }}
        />        
        <Page />
      </div>
      <Footer />
    </React.StrictMode>
  );
}

export { Manage };

function Page() {
  const history = useHistory();

  return (
    <Switch>
      <Route exact path="/manage" children={()=>(history.push('/manage/people'))} />
      <Route path="/manage/:page" children={<PageSelect />} />
    </Switch>
  )
}



function PageSelect(param) {
  let { page } = useParams();
  const pageList = ['people', 'photo', 'research', 'honor', 'publication']

  if (page === 'people') {
    return <People page={page} list={pageList}/>
  }
  else if (page === 'honor') {
    return <Honor page={page} list={pageList} />
  }
  else if (page === 'research') {
    return <Research page={page} list={pageList} />
  }
  else if (page === 'photo') {
    return <Photo page={page} list={pageList} />
  }
  else if (page === 'publication') {
    return <Publication page={page} list={pageList} />
  }
}